import { showLoadError } from "../../errors";
import { showUnsuccessFormMessage } from "../../alert";
import { ErrorResponse, snakeCaseNamesByCamelCaseMap } from "../../data/types";
import { selectTabByTabName, TabNames } from "../tabpanel/select-tab";
import { validateForm } from "./validate";

const BASE_URL =
  window.location.hostname === "localhost" ? "http://staging2.overvis.com" : "";
const URL = `${BASE_URL}/api/set_cnfs.json`;
// const SAVE_FILE_URL = "/api/cmd_conf_c2f";

export function rigSubmitForm(form: HTMLFormElement): void {
  validateForm(form)
    .onSuccess(async (e) => {
      e!.preventDefault();

      let result: undefined | ErrorResponse;
      try {
        result = await submit(form);
      } catch (err) {
        showLoadError((err as Error).message);
        return;
      }

      if (result?.error_descr) {
        showUnsuccessFormMessage(result.error_descr);
        return;
      }

      sessionStorage.setItem("formSubmited", "true");
      window.location.reload();
    })
    .onFail((fields) => {
      for (const key in fields) {
        const fieldItem = fields[key];
        if (!fieldItem.isValid) {
          // show tab with field's error
          selectTabByTabName(
            fieldItem.elem.getAttribute("data-tab-name") as TabNames
          );
          break;
        }
      }
    });
}

async function submit(
  form: HTMLFormElement
): Promise<ErrorResponse | undefined> {
  const fields = Array.from(form.querySelectorAll("[data-holds].chenged"));
  const snakeCaseNamesMap = snakeCaseNamesByCamelCaseMap();

  const params = [];
  for (const field of fields) {
    const name = field.attributes.getNamedItem("data-holds")!.value;
    const snakeCaseName = snakeCaseNamesMap.get(name);
    if (!snakeCaseName) {
      continue;
    }

    if (field instanceof HTMLInputElement && field.type === "checkbox") {
      const value = field.checked ? 1 : 0;
      params.push(`${snakeCaseName}=${value}`);
    } else if (
      field instanceof HTMLInputElement ||
      field instanceof HTMLSelectElement
    ) {
      params.push(`${snakeCaseName}=${field.value}`);
    }
  }

  if (params.length < 1) {
    return;
  }

  const resp = await fetch(URL, {
    method: "POST",
    headers: {
      "Content-Type": "application/x-www-form-urlencoded",
    },
    body: params.join("&"),
  });

  if (resp.body && "error_descr" in resp.body) {
    return resp.body as ErrorResponse;
  }

  return;
}
