import { loadListNif } from "../../data/load";
import { DHCPModes } from "../../data/types";

const DHCP_ETHERNET_SELECT_ID = "ethernet-dhcp";
const ETHERNET_CUSTOM_SETT_ID = "ethernet-custom-settings";
const ETHERNET_NETIF_SELECT_ID = "ethernet-netif-name";
const ETHERNET_NETIF_UPDT_BTN_ID = "ethernet-netif-update-btn";

export function rigChangeEthernetDHCP(): void {
  const select = document.querySelector(
    `#${DHCP_ETHERNET_SELECT_ID}`
  ) as HTMLSelectElement;

  const customSettEl = document.querySelector(`#${ETHERNET_CUSTOM_SETT_ID}`);

  select.addEventListener("change", () => {
    const value = Number(select.value) as DHCPModes;
    if (value === DHCPModes.Automatic) {
      customSettEl?.classList.add("d-none");
      return;
    }
    customSettEl?.classList.remove("d-none");
  });
}

export function rigUpdateEthernetNetifList(): void {
  const updateBtn = document.querySelector(
    `#${ETHERNET_NETIF_UPDT_BTN_ID}`
  ) as HTMLElement;
  updateBtn.addEventListener("click", async () => {
    updateBtn.querySelector(".bi-arrow-clockwise")?.classList.add("d-none");
    updateBtn.querySelector(".spinner-border")?.classList.remove("d-none");
    await fillEthernetNetifList();
    updateBtn.querySelector(".spinner-border")?.classList.add("d-none");
    updateBtn.querySelector(".bi-arrow-clockwise")?.classList.remove("d-none");
  });
}

export async function fillEthernetNetifList(): Promise<void> {
  const list = await loadListNif();

  const selectEl = document.querySelector(
    `#${ETHERNET_NETIF_SELECT_ID}`
  ) as HTMLSelectElement;
  const value = selectEl.getAttribute("value");

  selectEl.innerHTML = "";

  const defaultOption = document.createElement("option");
  defaultOption.value = "";
  defaultOption.text = "----";
  selectEl.appendChild(defaultOption);

  for (const item of list) {
    const option = document.createElement("option");
    option.value = item.devName;
    option.text = item.devName;
    option.selected = value === item.devName;
    selectEl.appendChild(option);
  }
}
