import { formatSizeUnits, getServerPathByServerName } from "../help";
import { StateData } from "../data/types";

const STATE_IMG_CONTAINER_ID = "state-img-container";
const ONLINE_ID = "online";
const OFFLINE_ID = "offline";
const NO_OVERVIS_ID = "no-overvis";
const DISABLE_OVERVIS_ID = "disabled-overvis";

export function updateStateImg(data: StateData): void {
  const stateContainer = document.querySelector(
    `#${STATE_IMG_CONTAINER_ID}`
  ) as HTMLElement;
  if (!stateContainer) {
    return;
  }

  stateContainer.classList.remove("d-none");

  if (navigator.onLine) {
    if (data.ovbState === 0) {
      setDisabledOvervis(stateContainer);
      setDisableOvervisValues(stateContainer, data);
    } else if (data.ovbState !== 3) {
      setNoOvervis(stateContainer);
      setNoOvervisValues(stateContainer, data);
    } else {
      setOnline(stateContainer);
      setOnlineValues(stateContainer, data);
    }
  } else {
    setOffline(stateContainer);
    setOfflineValues(stateContainer, data);
  }
}

function setOnline(stateContainer: HTMLElement): void {
  stateContainer.querySelector(`#${ONLINE_ID}`)?.classList.remove("d-none");
  stateContainer.querySelector(`#${NO_OVERVIS_ID}`)?.classList.add("d-none");
  stateContainer.querySelector(`#${OFFLINE_ID}`)?.classList.add("d-none");
  stateContainer
    .querySelector(`#${DISABLE_OVERVIS_ID}`)
    ?.classList.add("d-none");
}

function setNoOvervis(stateContainer: HTMLElement): void {
  stateContainer.querySelector(`#${ONLINE_ID}`)?.classList.add("d-none");
  stateContainer.querySelector(`#${NO_OVERVIS_ID}`)?.classList.remove("d-none");
  stateContainer.querySelector(`#${OFFLINE_ID}`)?.classList.add("d-none");
  stateContainer
    .querySelector(`#${DISABLE_OVERVIS_ID}`)
    ?.classList.add("d-none");
}

function setOffline(stateContainer: HTMLElement): void {
  stateContainer.querySelector(`#${ONLINE_ID}`)?.classList.add("d-none");
  stateContainer.querySelector(`#${NO_OVERVIS_ID}`)?.classList.add("d-none");
  stateContainer.querySelector(`#${OFFLINE_ID}`)?.classList.remove("d-none");
  stateContainer
    .querySelector(`#${DISABLE_OVERVIS_ID}`)
    ?.classList.add("d-none");
}

function setDisabledOvervis(stateContainer: HTMLElement): void {
  stateContainer.querySelector(`#${ONLINE_ID}`)?.classList.add("d-none");
  stateContainer.querySelector(`#${NO_OVERVIS_ID}`)?.classList.add("d-none");
  stateContainer.querySelector(`#${OFFLINE_ID}`)?.classList.add("d-none");
  stateContainer
    .querySelector(`#${DISABLE_OVERVIS_ID}`)
    ?.classList.remove("d-none");
}

function setOnlineValues(stateContainer: HTMLElement, data: StateData): void {
  // bind-acc-link
  // online-ethernet-ip
  // online-wifi-ip
  // online-activation-code
  // online-server-name
  // online-overvis-traffic
  // online-overvis-traffic-avg
  stateContainer.querySelector("#online-ethernet-ip")!.textContent =
    data.elanIp;
  stateContainer.querySelector("#online-wifi-ip")!.textContent = data.wlanIp;
  stateContainer.querySelector("#online-server-name")!.textContent =
    data.ovbCurHostname;

  stateContainer
    .querySelector("#bind-acc-link")!
    .setAttribute("href", `${getServerPathByServerName(data.ovbCurHostname)}/`);

  if (data.ovbActCode === 0) {
    stateContainer.querySelector("#online-activation-code")!.textContent =
      "connected";
  } else if (data.ovbActCode > 99999999) {
    stateContainer.querySelector("#online-activation-code")!.textContent =
      "waiting";
  } else if (!(data.ovbActCode < 10000000 || data.ovbActCode > 99999999)) {
    stateContainer.querySelector(
      "#online-activation-code"
    )!.textContent = `${data.ovbActCode}`;
    stateContainer
      .querySelector("#bind-acc-link")!
      .setAttribute(
        "href",
        `${getServerPathByServerName(data.ovbCurHostname)}/?ac=${
          data.ovbActCode
        }`
      );
    stateContainer.querySelector("#bind-acc-link")!.innerHTML =
      "Bind this device to your account on Overvis";
  } else {
    stateContainer.querySelector("#online-activation-code")!.textContent = "-";
  }

  // set overvis traffic
  stateContainer.querySelector("#online-overvis-traffic")!.textContent =
    getTrafficLine(data.ovbRxConn, data.ovbTxConn);

  stateContainer.querySelector("#online-overvis-traffic-avg")!.textContent =
    getAvgTrafficLine(data.ovbRxAvg, data.ovbTxAvg);

  // online-web-traffic
  stateContainer.querySelector("#online-web-traffic")!.textContent =
    getTrafficLine(
      data.mbtcpCli_0RxTotal +
        data.mbtcpCli_1RxTotal +
        data.mbtcpSrvRxTotal +
        data.ovbRxTotal,
      data.mbtcpCli_0TxTotal +
        data.mbtcpCli_1TxTotal +
        data.mbtcpSrvTxTotal +
        data.ovbTxTotal
    );

  stateContainer.querySelector("#online-web-traffic-avg")!.textContent =
    getAvgTrafficLine(
      data.mbtcpCli_0RxAvg +
        data.mbtcpCli_1RxAvg +
        data.mbtcpSrvRxAvg +
        data.ovbRxAvg,
      data.mbtcpCli_0TxAvg +
        data.mbtcpCli_1TxAvg +
        data.mbtcpSrvTxAvg +
        data.ovbTxAvg
    );
}

function setNoOvervisValues(
  stateContainer: HTMLElement,
  data: StateData
): void {
  // no-overvis-server-name
  // no-overvis-error
  // no-overvis-ethernet-ip
  // no-overvis-wifi-ip
  // no-overvis-status
  // no-overvis-conn-timeout
  stateContainer.querySelector("#no-overvis-ethernet-ip")!.textContent =
    data.elanIp;
  stateContainer.querySelector("#no-overvis-wifi-ip")!.textContent =
    data.wlanIp;
  stateContainer.querySelector("#no-overvis-server-name")!.textContent =
    data.ovbCurHostname;

  if (data.ovbLastErrorCode) {
    stateContainer.querySelector(
      "#no-overvis-error"
    )!.textContent = `${data.ovbLastErrorCode}`;
  }

  // no-overvis-web-traffic
  stateContainer.querySelector("#no-overvis-web-traffic")!.textContent =
    getTrafficLine(
      data.mbtcpCli_0RxTotal +
        data.mbtcpCli_1RxTotal +
        data.mbtcpSrvRxTotal +
        data.ovbRxTotal,
      data.mbtcpCli_0TxTotal +
        data.mbtcpCli_1TxTotal +
        data.mbtcpSrvTxTotal +
        data.ovbTxTotal
    );

  stateContainer.querySelector("#no-overvis-web-traffic-avg")!.textContent =
    getAvgTrafficLine(
      data.mbtcpCli_0RxAvg +
        data.mbtcpCli_1RxAvg +
        data.mbtcpSrvRxAvg +
        data.ovbRxAvg,
      data.mbtcpCli_0TxAvg +
        data.mbtcpCli_1TxAvg +
        data.mbtcpSrvTxAvg +
        data.ovbTxAvg
    );
}

function setOfflineValues(stateContainer: HTMLElement, data: StateData): void {
  // offline-ethernet-ip
  // offline-wifi-ip
  stateContainer.querySelector("#offline-ethernet-ip")!.textContent =
    data.elanIp;
  stateContainer.querySelector("#offline-wifi-ip")!.textContent = data.wlanIp;
}

function setDisableOvervisValues(
  stateContainer: HTMLElement,
  data: StateData
): void {
  // disabled-overvis-ethernet-ip
  // disabled-overvis-wifi-ip
  stateContainer.querySelector("#disabled-overvis-ethernet-ip")!.textContent =
    data.elanIp;
  stateContainer.querySelector("#disabled-overvis-wifi-ip")!.textContent =
    data.wlanIp;

  // disabled-web-traffic
  stateContainer.querySelector("#disabled-web-traffic")!.textContent =
    getTrafficLine(
      data.mbtcpCli_0RxTotal +
        data.mbtcpCli_1RxTotal +
        data.mbtcpSrvRxTotal +
        data.ovbRxTotal,
      data.mbtcpCli_0TxTotal +
        data.mbtcpCli_1TxTotal +
        data.mbtcpSrvTxTotal +
        data.ovbTxTotal
    );

  stateContainer.querySelector("#disabled-web-traffic-avg")!.textContent =
    getTrafficLine(
      data.mbtcpCli_0RxAvg +
        data.mbtcpCli_1RxAvg +
        data.mbtcpSrvRxAvg +
        data.ovbRxAvg,
      data.mbtcpCli_0TxAvg +
        data.mbtcpCli_1TxAvg +
        data.mbtcpSrvTxAvg +
        data.ovbTxAvg
    );
}

function getTrafficLine(rx: number, tx: number): string {
  const ovbRx = `${formatSizeUnits(rx)} rx`;
  const ovbTx = `${formatSizeUnits(tx)} tx `;

  return `${ovbRx} / ${ovbTx}`;
}

function getAvgTrafficLine(rxAvg: number, txAvg: number): string {
  const ovbRxAvg = `${formatSizeUnits(rxAvg / 60)}/s rx`;
  const ovbTxAvg = `${formatSizeUnits(txAvg / 60)}/s tx`;

  return `${ovbRxAvg} / ${ovbTxAvg}`;
}
