import {
  getInterfaceType,
  ModbusCommand,
  MODBUS_REQUESTS_TEXT_ID,
} from "./index";
import { SettingsData } from "../data/types";
import { validateConstructorForm } from "./validation-constructor-form";
import { getFormValues, numberToHex16, numberToHex8 } from "../help";

const MODBUS_COMMAND_SELECT_ID = "command-select";
const ADDRESSES_QUANTITY_CONTAINER_ID = "addresses-quantity-container";
const VALUES_CONTAINER_ID = "values-container";
const ADD_VALUE_BTN = "add-value-btn";
const MODBUS_VALUES_CONTAINER_ID = "modbus-values-container";
const MODBUS_VALUE_ITEM_ID = "value-container-template";
const CONSTRUCTOR_FORM_ID = "constructor-form";

interface ConstructorFormData {
  deviceId: number;
  command: ModbusCommand;
  address: number;
  quantity?: number;
  modbusValues?: number | number[];
}

export function initRequestConstructor(
  container: HTMLElement,
  settingsData: SettingsData
): void {
  rigChangeModbusCommand(container);
  rigAddValueField(container);
  rigSubmitConstructorForm(container, settingsData);
}

function rigChangeModbusCommand(container: HTMLElement): void {
  const selectEl = container.querySelector(
    `#${MODBUS_COMMAND_SELECT_ID}`
  ) as HTMLSelectElement;
  const valuesContEl = container.querySelector(
    `#${VALUES_CONTAINER_ID}`
  ) as HTMLElement;
  const addQuantity = container.querySelector(
    `#${ADDRESSES_QUANTITY_CONTAINER_ID}`
  ) as HTMLElement;

  selectEl.addEventListener("change", () => {
    const value = selectEl.value as ModbusCommand;
    if (
      value === ModbusCommand.WriteSingleCoil ||
      value === ModbusCommand.WriteSingleRegister
    ) {
      valuesContEl.classList.remove("d-none");
      addQuantity.classList.add("d-none");
      setOnlyOneValueField(container);
      (
        container.querySelector(`#${ADD_VALUE_BTN}`) as HTMLButtonElement
      ).classList.add("d-none");
      return;
    } else if (
      value === ModbusCommand.WriteMultipleCoils ||
      value === ModbusCommand.WriteMultipleRegisters
    ) {
      valuesContEl.classList.remove("d-none");
      addQuantity.classList.add("d-none");
      (
        container.querySelector(`#${ADD_VALUE_BTN}`) as HTMLButtonElement
      ).classList.remove("d-none");
      return;
    }

    valuesContEl.classList.add("d-none");
    addQuantity.classList.remove("d-none");
  });
}

function rigAddValueField(container: HTMLElement): void {
  const btn = container.querySelector(`#${ADD_VALUE_BTN}`) as HTMLButtonElement;
  const valueClone = (
    container.querySelector(`#${MODBUS_VALUE_ITEM_ID}`) as HTMLElement
  ).cloneNode(true) as HTMLElement;
  (container.querySelector(`#${MODBUS_VALUE_ITEM_ID}`) as HTMLElement).remove();

  addValueItem(container, valueClone);
  btn.addEventListener("click", () => {
    addValueItem(container, valueClone);
  });
}

function rigSubmitConstructorForm(
  container: HTMLElement,
  settingsData: SettingsData
): void {
  const form = container.querySelector(
    `#${CONSTRUCTOR_FORM_ID}`
  ) as HTMLFormElement;
  validateConstructorForm(form).onSuccess((e) => {
    e!.preventDefault();
    const data = proccessFormData(getFormValues<ConstructorFormData>(form));

    const reuestsField = container.querySelector(
      `#${MODBUS_REQUESTS_TEXT_ID}`
    ) as HTMLTextAreaElement;

    const reqType = getInterfaceType(container, settingsData);
    const oldRequsts = reuestsField.value;
    let newReq = "";
    if (reqType === "tcp") {
      const reqArr = oldRequsts.split("\n");
      const bytesHexArr = reqArr[reqArr.length - 1].split(":");
      const lastTransId =
        Number(bytesHexArr[0]) && Number(bytesHexArr[1])
          ? 0
          : parseInt(`${bytesHexArr[0]}${bytesHexArr[1]}`, 16) + 1;
      // create tcp request
      newReq = getTcpRequest(data, lastTransId);
    } else if (reqType === "rtu") {
      newReq = getRtuRequest(data);
    } else {
      newReq = getAsciiRequest(data);
    }
    reuestsField.value = oldRequsts ? `${oldRequsts}\n${newReq}` : newReq;
  });
}

function addValueItem(container: HTMLElement, valueClone: HTMLElement): void {
  const valuesContainer = document.querySelector(
    `#${MODBUS_VALUES_CONTAINER_ID}`
  ) as HTMLElement;

  const item = valueClone.cloneNode(true) as HTMLElement;
  item.classList.remove("d-none");

  const removeBtn = item.querySelector(
    ".remove-value-btn"
  ) as HTMLButtonElement;
  removeBtn.addEventListener("click", () => {
    item.remove();
    const values = Array.from(container.querySelectorAll(".value-item"));
    if (values.length === 1) {
      values[0].querySelector(".remove-value-btn")?.classList.add("d-none");
    }
  });

  valuesContainer.appendChild(item);

  const values = Array.from(container.querySelectorAll(".value-item"));
  if (values.length >= 2) {
    for (const valEl of values) {
      valEl.querySelector(".remove-value-btn")?.classList.remove("d-none");
    }
  }
}

function setOnlyOneValueField(container: HTMLElement): void {
  const values = Array.from(container.querySelectorAll(".value-item"));
  const showingEl = values.splice(0, 1)[0];
  for (const valEl of values) {
    valEl?.remove();
  }
  showingEl.querySelector(".remove-value-btn")?.classList.add("d-none");
}

export function getTcpRequest(data: number[], lastTransId: number): string {
  const tid = numberToHiLowBytes(lastTransId);
  const pid = numberToHiLowBytes(0);
  data.unshift(...numberToHiLowBytes(data.length));
  data.unshift(...pid);
  data.unshift(...tid);
  return data.map(numberToHex8).join(":");
}

export function getRtuRequest(data: number[]): string {
  const crc16 = numberToHex16(getCrc16(data));
  return `${data.map(numberToHex8).join(":")}:${crc16}`;
}

export function getAsciiRequest(data: number[]): string {
  const hexData = Array.from(data.map(numberToHex8).join(""))
    .map((h) => h.charCodeAt(0))
    .map(numberToHex8);
  const lrc = numberToHex16(getLrc8(data));
  return `3a:${hexData.join(":")}:${lrc}:0d:0a`;
}

function proccessFormData(data: ConstructorFormData): number[] {
  const result = [
    Number(data.deviceId),
    Number(data.command),
    ...numberToHiLowBytes(data.address),
  ];
  if (data.modbusValues) {
    if (Array.isArray(data.modbusValues)) {
      const values: number[] = [];
      for (const val of data.modbusValues) {
        values.push(...numberToHiLowBytes(Number(val)));
      }
      const quantity = values.length / 2;
      result.push(...numberToHiLowBytes(quantity));
      const bytes = values.length;
      result.push(bytes);
      result.push(...values);
    } else {
      result.push(...numberToHiLowBytes(Number(data.modbusValues)));
    }
  } else {
    result.push(...numberToHiLowBytes(data.quantity || 1));
  }
  return result;
}

function numberToHiLowBytes(value: number): [number, number] {
  return [(value >> 8) & 0xff, value & 0xff];
}

function getCrc16(data: number[]): number {
  let crc = 0xffff;
  for (let b of data) {
    for (let i = 0; i < 8; i++) {
      const first8b = crc & 0xff;
      crc = (b ^ first8b) & 1 ? (crc >> 1) ^ 0xa001 : crc >> 1;
      b >>= 1;
    }
  }
  return crc;
}

function getLrc8(data: number[]): number {
  let lrc = 0;
  for (let b of data) {
    let first8b = b & 0xff;
    lrc += first8b++;
  }
  return 0xff - lrc + 1;
}
