const SELECT_PLUS_CLASS_NAME = "select-plus";
const PLUS_CLASS_NAME = "plus";

export function rigSelectPlus(): void {
  const elems = Array.from(
    document.querySelectorAll(`.${SELECT_PLUS_CLASS_NAME}`)
  );
  for (const el of elems) {
    const plusBtn = el.querySelector(`.${PLUS_CLASS_NAME}`);
    plusBtn?.addEventListener("click", () => {
      const name = prompt("Please enter name:")?.trim();
      if (!name) {
        return;
      }

      const optionEl = document.createElement("option");
      optionEl.value = name;
      optionEl.text = name;

      const selectEl = el.querySelector("select") as HTMLSelectElement;
      selectEl.appendChild(optionEl);
      selectEl.value = name;
      selectEl.dispatchEvent(new Event("change"));
    });
  }
}
