import "bootstrap/dist/js/bootstrap.min.js";
import { initState } from "./state";
import { initForm } from "./form";
import { initActions } from "./actions";
import { initMenu } from "./menu";
import { hideGlobalSpinner, showGlobalSpinner } from "./spinner";
import { initModbus } from "./modbus";

window.onload = async () => {
  showGlobalSpinner();
  initMenu();
  initActions();
  await initForm();
  await initState();
  await initModbus();
  hideGlobalSpinner();
};
