export type ListNifResponse =
  | {
      list_nif: NetifListDataResponse[];
    }
  | ErrorResponse;

export type Rs485NetifListResponse =
  | {
      list_ser: NetifListDataResponse[];
    }
  | ErrorResponse;

interface NetifListDataResponse {
  type: string;
  dev_name: string;
  mac?: string;
}

export interface NetifListData {
  type: string;
  devName: string;
  mac?: string;
}

export type WifiSsidResponse =
  | {
      list_ap: WifiSsidList[];
    }
  | ErrorResponse;

export interface WifiSsidList {
  ssid: string;
  mac: string;
  signal: number;
}

export enum WifiModes {
  Disabled = 0,
  Client = 1,
  AccessPoint = 2,
}

export enum DHCPModes {
  Custom = 0,
  Automatic = 1,
}

enum Rs485Modes {
  MasterRTU = 0,
  MasterASCII = 1,
  SlaveRTU = 2,
  SlaveASCII = 3,
}

enum ModbusInterfaces {
  Disabled = 0,
  Loopback = 1,
  ELAN = 2,
  WLAN = 3,
  Any = 255,
}

