const MENU_ID = "main-menu";
const MOBILE_MENU_ID = "mobile-menu";

export function initMenu(): void {
  const menuContainer = document.querySelector(`#${MENU_ID}`) as HTMLElement;
  const mobileMenu = document.querySelector(
    `#${MOBILE_MENU_ID}`
  ) as HTMLElement;
  const pathName = window.location.pathname.split(".")[0].replace("/", "");

  menuContainer.querySelector(`.active`)?.classList.remove("active");
  mobileMenu.querySelector(`.active`)?.classList.remove("active");
  if (!pathName) {
    menuContainer.querySelector(`#index-menu-link`)?.classList.add("active");
    mobileMenu.querySelector(`#index-menu-link`)?.classList.add("active");
    return;
  }

  menuContainer
    .querySelector(`#${pathName}-menu-link`)
    ?.classList.add("active");

  mobileMenu.querySelector(`#${pathName}-menu-link`)?.classList.add("active");
}
