import { loadSettingsData, loadStateData } from "../data/load";
import { SettingsData, StateData } from "../data/types";
import {
  getAsciiRequest,
  getRtuRequest,
  getTcpRequest,
  initRequestConstructor,
} from "./constructor";

const MODBUS_CONTAINER_ID = "modbus-container";
export const INTERFACE_OPTIONS_SELECT_ID = "interface-select";
export const MODBUS_REQUESTS_TEXT_ID = "modbus-request";
const MODBUS_FORM_ID = "modbus-req-form";
const RESPONSES_CONTAINER_ID = "responses-container";
const RESPONSES_LIST_ID = "responses-list";

const BASE_URL =
  window.location.hostname === "localhost" ? "http://staging2.overvis.com" : "";
const URL = `${BASE_URL}/api/mbgate.json`;

type ResponseData =
  | {
      response: string;
    }
  | {
      error_code: number;
      error_descr: string;
    };

export enum ModbusCommand {
  ReadCoils = "0x01",
  ReadDiscreteInputs = "0x02",
  ReadHoldingRegisters = "0x03",
  ReadInputRegisters = "0x04",
  WriteSingleCoil = "0x05",
  WriteSingleRegister = "0x06",
  WriteMultipleCoils = "0x0F",
  WriteMultipleRegisters = "0x10",
}

export enum RequestType {
  Opcb = "opcb",
  Mbtcp0 = "mbtcp0",
  Mbtcp1 = "mbtcp1",
  Rs4850 = "rs4850",
  Rs4851 = "rs4851",
}

export async function initModbus(): Promise<void> {
  const container = document.querySelector(
    `#${MODBUS_CONTAINER_ID}`
  ) as HTMLElement;
  if (!container) {
    return;
  }

  const settingsData = await loadSettingsData();
  const stateData = await loadStateData();

  initRequest(container, settingsData, stateData);
  initRequestConstructor(container, settingsData);
}

function initRequest(
  container: HTMLElement,
  settingsData: SettingsData,
  stateData: StateData
): void {
  rigChangeInterfaveType(container);
  rigSubmitForm(container, settingsData);
  setIntarfaceOptions(container, settingsData, stateData);
}

function rigChangeInterfaveType(container: HTMLElement): void {
  const selectEl = container.querySelector(
    `#${INTERFACE_OPTIONS_SELECT_ID}`
  ) as HTMLSelectElement;
  const reuestsField = container.querySelector(
    `#${MODBUS_REQUESTS_TEXT_ID}`
  ) as HTMLTextAreaElement;
  selectEl.addEventListener("change", () => {
    reuestsField.value = "";
  });
}

function rigSubmitForm(
  container: HTMLElement,
  settingsData: SettingsData
): void {
  const form = container.querySelector(`#${MODBUS_FORM_ID}`) as HTMLFormElement;
  form.addEventListener("submit", async (e) => {
    e.preventDefault();
    await submitForm(form, settingsData);
  });
}

async function submitForm(
  form: HTMLElement,
  settingsData: SettingsData
): Promise<void> {
  const requests = (
    form.querySelector(`#${MODBUS_REQUESTS_TEXT_ID}`) as HTMLTextAreaElement
  ).value.split("\n");
  const reqType = getInterfaceType(form, settingsData);

  form.querySelector(`#${RESPONSES_CONTAINER_ID}`)?.classList.remove("d-none");

  let lastTcpTransId: undefined | number;
  for (const request of requests) {
    let reqBody = "";
    if (reqType === "tcp") {
      reqBody = getTcpBodyFromReq(request);
      lastTcpTransId = lastTcpTransId === undefined ? 0 : lastTcpTransId + 1;
    } else if (reqType === "ascii") {
      reqBody = getAsciiBodyFormReq(request);
    } else {
      reqBody = getRtuBodyFromReq(request);
    }

    const response = await fetch(`${URL}?request=${reqBody}`);
    const result = JSON.parse(await response.text()) as ResponseData;
    if ("error_code" in result) {
      showResonse(request, result.error_descr);
    } else {
      showResonse(
        request,
        getResponseBytesStr(result.response, reqType, lastTcpTransId)
      );
    }
  }
}

function showResonse(request: string, responseResult: string): void {
  const respListEl = document.querySelector(`#${RESPONSES_LIST_ID}`)!;
  const item = document.createElement("div");
  item.classList.add("response-item");
  item.innerHTML = `${request} > ${responseResult}`;
  respListEl.appendChild(item);
}

function getResponseBytesStr(
  response: string,
  reqType: "tcp" | "rtu" | "ascii",
  lastTcpTransId?: number
): string {
  const bytes: string[] = [];
  while (response.length > 0) {
    bytes.push(response.slice(0, 2));
    response = response.substring(2);
  }
  let result = "";
  const numbers = bytes.map((b) => Number(`0x${b}`));
  if (reqType === "tcp") {
    console.log(lastTcpTransId);
    result = getTcpRequest(numbers, lastTcpTransId || 0);
  } else if (reqType === "ascii") {
    result = getAsciiRequest(numbers);
  } else {
    result = getRtuRequest(numbers);
  }
  return result;
}

function setIntarfaceOptions(
  container: HTMLElement,
  settingsData: SettingsData,
  stateData: StateData
): void {
  const selectEl = container.querySelector(
    `#${INTERFACE_OPTIONS_SELECT_ID}`
  ) as HTMLSelectElement;

  if (settingsData?.mbgateViuEnabled) {
    const option = document.createElement("option");
    option.value = "opcb";
    option.text = "OPCP-221 (Modbus TCP)";
    selectEl.appendChild(option);
  }

  if (
    settingsData?.mbtcpCli0NetifType &&
    (stateData?.mbtcpCli_0Netif === 2 || stateData?.mbtcpCli_0Netif === 3)
  ) {
    const option = document.createElement("option");
    const ipPort = `${settingsData.mbtcpCli0RemoteServerIp}:${settingsData.mbtcpCli0RemoteServerPort}`;
    const mode = stateData?.mbtcpCli_0Netif === 2 ? "Ethernet" : "WIFI";
    option.value = "mbtcp0";
    option.text = `${mode} ${ipPort}`;
    selectEl.appendChild(option);
  }

  if (
    settingsData?.mbtcpCli1NetifType &&
    (stateData?.mbtcpCli_1Netif === 2 || stateData?.mbtcpCli_1Netif === 3)
  ) {
    const option = document.createElement("option");
    const ipPort = `${settingsData.mbtcpCli1RemoteServerIp}:${settingsData.mbtcpCli1RemoteServerPort}`;
    const mode = stateData?.mbtcpCli_1Netif === 2 ? "Ethernet" : "WIFI";
    option.value = "mbtcp1";
    option.text = `${mode} ${ipPort}`;
    selectEl.appendChild(option);
  }

  if (
    settingsData?.rs4850DevName &&
    settingsData?.rs4850Mode !== 2 &&
    settingsData?.rs4850Mode !== 3
  ) {
    const option = document.createElement("option");
    option.value = "rs4850";
    option.text = `${settingsData?.rs4850DevName} (${getRS485ModeName(
      settingsData.rs4850Mode
    )})`;
    selectEl.appendChild(option);
  }

  if (
    settingsData?.rs4851DevName &&
    settingsData?.rs4851Mode !== 2 &&
    settingsData?.rs4851Mode !== 3
  ) {
    const option = document.createElement("option");
    option.value = "rs4851";
    option.text = `${settingsData?.rs4851DevName} (${getRS485ModeName(
      settingsData.rs4851Mode
    )})`;
    selectEl.appendChild(option);
  }
}

function getTcpBodyFromReq(request: string): string {
  const [_tid0, _tid1, _pid0, _pid1, _len0, _len1, ...body] =
    request.split(":");
  return body.join("");
}

function getRtuBodyFromReq(request: string): string {
  // remove crc
  return request.split(":").slice(0, -2).join("");
}

function getAsciiBodyFormReq(request: string): string {
  // remove lrc, :, \r\n
  const ascii = request.split(":").slice(0, -4).slice(1);
  let result = "";
  for (const code of ascii) {
    result += String.fromCharCode(Number(`0x${code}`));
  }
  return result;
}

function getRS485ModeName(val: 0 | 1): string {
  if (val === 0) {
    return "RTU Master";
  }
  return "ASCII Master";
}

export function getInterfaceType(
  container: HTMLElement,
  settingsData: SettingsData
): "tcp" | "rtu" | "ascii" {
  const reqType = (
    container.querySelector(
      `#${INTERFACE_OPTIONS_SELECT_ID}`
    ) as HTMLSelectElement
  ).value;
  if (
    reqType === RequestType.Opcb ||
    reqType === RequestType.Mbtcp0 ||
    reqType === RequestType.Mbtcp1
  ) {
    return "tcp";
  }

  if (
    (reqType === RequestType.Rs4850 && settingsData.rs4850Mode === 1) ||
    (reqType === RequestType.Rs4851 && settingsData.rs4851Mode === 1)
  ) {
    return "ascii";
  }

  return "rtu";
}
