import { selectTabByTabName, TabNames } from "./select-tab";

export function rigDropdownMenu(): void {
  const btns = document.querySelectorAll(".dropdown-item");
  for (const btn of btns) {
    btn.addEventListener("click", () => {
      document
        .querySelector(".dropdown-item.active")
        ?.classList.remove("active");
      btn.classList.add("active");
      const tabName = btn.getAttribute("data-tab-name") as TabNames;
      selectTabByTabName(tabName);
    });
  }
}
