import { hideRebootDeviceAlert, showRebootDeviceAlert } from "../alert";
import { StateData } from "../data/types";

const STATE_ITEM_CLASS_NAME = "state-item";

export function updateStateTable(data: StateData): void {
  const stateElems = document.querySelectorAll(`.${STATE_ITEM_CLASS_NAME}`);
  for (const el of stateElems) {
    const dataName = el.getAttribute("data-value-name")!;
    el.querySelector(".value")!.innerHTML = data[dataName];
  }

  setStatusActivationCode(data.ovbActCode);
  setOvervisConnectionStatus(data.ovbState);
  setRuntime(data.osRunTime);

  if (data.confChanged) {
    showRebootDeviceAlert();
  } else {
    hideRebootDeviceAlert();
  }
}

function setStatusActivationCode(code: number): void {
  const el = document.querySelector("#activation-code");
  if (!el) {
    return;
  }
  if (code < 10000000 || code > 99999999) {
    el.innerHTML = "-";
  }
}

function setOvervisConnectionStatus(status: number): void {
  const el = document.querySelector("#overvis-connection-status");
  if (!el) {
    return;
  }
  if (status === 0) {
    el.innerHTML = "canceled in settings";
  } else if (status === 1) {
    el.innerHTML = "connection pause";
  } else if (status === 2) {
    el.innerHTML = "connection established";
  } else if (status === 3) {
    el.innerHTML = "connected";
  } else {
    el.innerHTML = "not available";
  }
}

function setRuntime(sec: number): void {
  const el = document.querySelector("#runtime-value");
  if (!el) {
    return;
  }
  el.innerHTML = secondsToDhms(sec);
}

function secondsToDhms(sec: number): string {
  var d = Math.floor(sec / (3600 * 24));
  var h = Math.floor((sec % (3600 * 24)) / 3600);
  var m = Math.floor((sec % 3600) / 60);
  var s = Math.floor(sec % 60);
  return `${d}d ${h}h ${m}m ${s}s`;
}
