export function showLoadError(text: string): void {
  if (text === "The user aborted a request.") {
    text = "Request timeout.";
  }
  document.querySelector("#error-text")!.innerHTML = text;
  document.querySelector("#load-error")?.classList.remove("d-none");
}

export function hideLoadError(): void {
  document.querySelector("#load-error")?.classList.add("d-none");
}

export function showDisconnectError(): void {
  document.querySelector("#disconnect-error")!.classList.remove("d-none");
}

export function hideDisconnectError(): void {
  document.querySelector("#disconnect-error")!.classList.add("d-none");
}
