import { hideLoadError, showLoadError } from "../errors";
import { loadStateData } from "../data/load";
import { hideGlobalSpinner } from "../spinner";
import { updateStateImg } from "./img";
import { updateStateTable } from "./table";

const UPDATE_INTERVAL = 1000;
let IS_REQUEST_ACTIVE = false;

// TODO: traffic to two line+
// TODO: image for wifi access point (wifi icon with ssid)
// TODO: image for rs-485
// TODO: activation code: if code === 0 (connected), if code > 99999999 (waiting)+
// TODO: add link to server+
// TODO: get server link from https:// serverName+

export async function initState(): Promise<void> {
  await setValues();
  rigUpdateValues();
}

async function setValues(): Promise<void> {
  if (sessionStorage.getItem("isReboot")) {
    await delay(3000);
  }

  let data;
  try {
    data = await loadStateData();
  } catch (e) {
    const text = (e as Error).message;
    showLoadError(text);
    return;
  }

  if (sessionStorage.getItem("isReboot")) {
    hideLoadError();
    // remove global spinner
    hideGlobalSpinner();
    sessionStorage.removeItem("isReboot");
  }

  updateStateImg(data);
  updateStateTable(data);
}

function rigUpdateValues(): void {
  window.setInterval(async () => {
    if (IS_REQUEST_ACTIVE) {
      return;
    }
    IS_REQUEST_ACTIVE = true;
    await setValues();
    IS_REQUEST_ACTIVE = false;
  }, UPDATE_INTERVAL);
}

async function delay(ms: number): Promise<void> {
  return new Promise<void>((resolve) => window.setTimeout(resolve, ms));
}
