const SUCCESS_ALERT_ID = "success-alert";
const UNSUCCESS_ERROR_ID = "unsuccess-alert";
const REBOOT_DEVICE_ALERT_ID = "reboot-device-alert";

export function showSuccessFormMessage(): void {
  const formIsSaved = sessionStorage.getItem("formSubmited");
  if (!formIsSaved) {
    return;
  }

  const alert = document.querySelector(`#${SUCCESS_ALERT_ID}`);
  alert?.classList.remove("d-none");

  sessionStorage.removeItem("formSubmited");
}

export function showUnsuccessFormMessage(msg: string): void {
  const alert = document.querySelector(`#${UNSUCCESS_ERROR_ID}`) as HTMLElement;
  alert.querySelector(".alert-desc")!.innerHTML = msg;
}

export function showRebootDeviceAlert(): void {
  document
    .querySelector(`#${REBOOT_DEVICE_ALERT_ID}`)
    ?.classList.remove("d-none");
}

export function hideRebootDeviceAlert(): void {
  document.querySelector(`#${REBOOT_DEVICE_ALERT_ID}`)?.classList.add("d-none");
}
