export async function loadStateData(): Promise<StateData> {
  const resp = await load<StateResponse>(GET_STATE_DATA_URL, REQ_TIMEOUT_MIN);

  if ("error_descr" in resp) {
    throw new Error(`${resp.error_descr}`);
  }
  const data = resp;
  return {
    accessLevel: data.access_level,
    devType: data.dev_type,
    elanIp: data.elan_ip,
    fwVer: data.ver,
    mac: data.mac,
    mbcl0Netif: data.mbcl_0_netif,
    mbcl1Netif: data.mbcl_1_netif,
    mbsrvClients: data.mbtcp_srv_clients,
    ovbActCode: data.ovb_act_code,
    ovbNetif: data.ovb_netif,
    wlanIp: data.wlan_ip,
    ovbState: data.ovb_state,
    confChanged: data.conf_changed,
    osRunTime: data.os_run_time,
    ovbCurHostname: data.ovb_cur_hostname,
    ovbLastErrorCode: data.ovb_last_error_code,
    ovbRxConn: data.ovb_rx_conn,
    ovbTxConn: data.ovb_tx_conn,
    ovbRxAvg: data.ovb_rx_avg,
    ovbTxAvg: data.ovb_tx_avg,
    mbtcpCli_0RxTotal: data.mbtcp_cli_0_rx_total,
    mbtcpCli_1RxTotal: data.mbtcp_cli_1_rx_total,
    mbtcpSrvRxTotal: data.mbtcp_srv_rx_total,
    ovbRxTotal: data.ovb_rx_total,
    mbtcpCli_0TxTotal: data.mbtcp_cli_0_tx_total,
    mbtcpCli_1TxTotal: data.mbtcp_cli_1_tx_total,
    mbtcpSrvTxTotal: data.mbtcp_srv_tx_total,
    ovbTxTotal: data.ovb_tx_total,
    mbtcpCli_0RxAvg: data.mbtcp_cli_0_rx_avg,
    mbtcpCli_1RxAvg: data.mbtcp_cli_1_rx_avg,
    mbtcpSrvRxAvg: data.mbtcp_srv_rx_avg,
    mbtcpCli_0TxAvg: data.mbtcp_cli_0_tx_avg,
    mbtcpCli_1TxAvg: data.mbtcp_cli_1_tx_avg,
    mbtcpSrvTxAvg: data.mbtcp_srv_tx_avg,
    mbtcpCli_0Netif: data.mbtcp_cli_0_netif,
    mbtcpCli_1Netif: data.mbtcp_cli_1_netif,
  };
}

export async function loadListNif(): Promise<NetifListData[]> {
  const resp = await load<ListNifResponse>(LIST_NIF_URL, REQ_TIMEOUT_MAX);

  if ("error_descr" in resp) {
    throw new Error(`${resp.error_descr}`);
  }

  const data = resp.list_nif;
  return data.map((r) => ({
    type: r.type,
    devName: r.dev_name,
    mac: r.mac,
  }));
}

export async function loadListSer(): Promise<NetifListData[]> {
  const resp = await load<Rs485NetifListResponse>(
    LIST_SER_URL,
    REQ_TIMEOUT_MAX
  );

  if ("error_descr" in resp) {
    throw new Error(`${resp.error_descr}`);
  }

  const data = resp.list_ser;
  return data.map((r) => ({
    type: r.type,
    devName: r.dev_name,
    mac: r.mac,
  }));
}

export async function loadWifiSsidNames(): Promise<WifiSsidList[]> {
  const resp = await load<WifiSsidResponse>(
    LIST_WIFI_SSID_NAMES_URL,
    REQ_TIMEOUT_MAX
  );

  if ("error_descr" in resp) {
    throw new Error(`${resp.error_descr}`);
  }

  return resp.list_ap;
}

async function load<T>(url: string, timeout: number): Promise<T> {
  const controller = new AbortController();
  const timeoutId = setTimeout(() => {
    controller.abort();
    // show disconnect error
    showDisconnectError();
  }, timeout);

  const resp = await fetch(url, { signal: controller.signal });
  clearTimeout(timeoutId);
  hideDisconnectError();

  const body = await resp.text();
  if (!body) {
    throw new Error("Empty result.");
  }

  return JSON.parse(body) as T;
}
